import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function workingCapitalTurnoverRatioTtm(tableName: string) {
  const title = "Working Capital Turnover (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["workingCapitalTurnoverRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Sales per dollar of working capital.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about working capital turnover?`,
              value:
                info.row.original.financialData.companyRatios?.data?.workingCapitalTurnoverRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            info.row.original.financialData.companyRatios?.data?.workingCapitalTurnoverRatioTtm
          }
        />
      )
    },
  })
}

export { workingCapitalTurnoverRatioTtm }
